import React from 'react';
import {action} from "mobx";
import {observer} from "mobx-react";
import qs from 'querystring';
import axios from "axios";
import config from "../config";
import model from "../model";

import "./loginview.css";

// https://www.robinwieruch.de/create-react-app-mobx-decorators
class LoginView extends React.Component{
    state = {
        username: '',
        password: '',
        hostname: 'http://localhost:5000',
    }
    
    onSubmit(event) {
        config.API_Host = this.state.hostname;
        
        event.preventDefault();
        const body = {
            username: this.state.username,
            password: this.state.password,
        }
        
        axios.post(config.API_Host + "/login", qs.stringify(body), {
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            }
        }).then(action((res) => {
            console.log("response from /login: " + JSON.stringify(res.data));
            model.state.user = {
                token: res.data.token,
                username: this.state.username,
                password: this.state.password,
            }
            model.state.error = null;
        })).catch(action((err) => {
            console.log(err);
            if (!err.response) {
                model.state.error = "Uh oh. Failed to connect to the host.";
            } else {
                model.state.error = "failed to login, status code: " + err.response.status;
            }
        }));
    }

    onChange(prop, event) {
        const state = Object.assign({}, this.state);
        state[prop] = event.target.value;
        this.setState(state);
    }

    render() {
        return (
            <div className="loginformcontainer">
                <form onSubmit = {this.onSubmit.bind(this)}>
                    <h3>Login</h3>
                    <span>Hostname</span>
                    <input name="hostname" 
                           type="text" 
                           onChange={this.onChange.bind(this, "hostname")}
                           value={this.state.hostname} />
                    <span>Username</span>
                    <input 
                        name="username" 
                        type="text" 
                        onChange = {this.onChange.bind(this, "username")}
                        value={this.state.username}
                        />
                    <br />
                    <span>Password</span>
                    <input 
                        name="password" 
                        type="text" 
                        onChange = {this.onChange.bind(this, "password")}
                        value={this.state.password} />
                    <br/>
                    <input 
                        type="submit" 
                        />
                </form>
            </div>
        )
    }
}

export default observer(LoginView);