import React from 'react';
import {action} from "mobx";
import {observer} from "mobx-react";
import model from "../model";

export default observer(() => {
    return(
        <ul>
            {model.state.connectedUsers.map((user) => {
                return (
                    <li key={user.name}>{user.name}</li>
                );
            })}
        </ul>
    )
});