import state from "./state"
import initialstate from "./initial-state";
import {action} from "mobx";

const model = {};
model.state = state

model.formatDate = (timestamp) => {
    var date = new Date(timestamp * 1000);
    return (
        date.toLocaleDateString("en-US") +
        " " +
        date.toLocaleTimeString("en-US")
    );
}

model.logout = action((logout) => {
    Object.assign(model.state, initialstate);
});

model.disconnect = action(() => {
    model.state.connectedToChat = false;
    model.state.connectedUsers = [];
})

model.connectToChat = (streamurl) => {
    const stream = new EventSource(
        streamurl
    );

    stream.addEventListener(
        "Disconnect",
        action(event => {
            stream.close();
            model.logout();
        }),
        false
    );

    stream.addEventListener(
        "Join",
        action(event => {
            var data = JSON.parse(event.data);
            if (!model.state.connectedUsers.find((user) => user.name === data.user)) {
                model.state.connectedUsers.push({name:data.user})
            }
            
            model.state.messages.push({
                timestamp: model.formatDate(data["created"]),
                message: " JOIN: " + data.user,
                id: event.id,
            })
        }),
        false
    );

    stream.addEventListener(
        "Message",
        action(event => {
            var data = JSON.parse(event.data);
            console.log(data);
            model.state.messages.push({
                timestamp: model.formatDate(data["created"]),
                message: " (" + data.user + ") " + data.message,
                id: event.id,
            })
        }),
        false
    );

    stream.addEventListener(
        "Part",
        action(event => {
            var data = JSON.parse(event.data);

            for (const useridx in model.state.connectedUsers){
                if (model.state.connectedUsers[useridx].name == data.user) {
                    model.state.connectedUsers.splice(useridx, 1);
                }
            }
            model.state.messages.push({
                timestamp: model.formatDate(data["created"]),
                message: " PART: " + data.user,
                id: event.id,
            })
        }),

        false
    );

    stream.addEventListener(
        "ServerStatus",
        action(event => {
            var data = JSON.parse(event.data);
            model.state.messages.push({
                timestamp: model.formatDate(data["created"]),
                message: " STATUS: " + data.status,
                id: event.id,
            })
        }),
        false
    );

    stream.addEventListener(
        "Users",
        action(event => {
            model.state.connectedToChat = true;
            model.state.connectedUsers = JSON.parse(event.data).users.map(user => {
                return {
                    name: user,
                }
            });
        }),
        false
    );

    stream.addEventListener(
        "error",
        action(event=> {
            model.disconnect();
            if (event.target.readyState == 2) {
                model.logout();
            } else {
                console.log("Disconnected, retrying");
            }
        }),
        false
    );
}



export default model;
