import React from "react";

export default (props) => {
    if (!props.error) {
        return (<div></div>)
    }
    
    return (
        <div style={{
            borderStyle: "solid",
            borderColor: "red",
            padding: "0.25em",
            width: "100%",
        }}>
            <span style={{color: "red", fontSize: "2em"}}>{props.error}</span>
        </div>
    )
}