import React from 'react';
import {action} from "mobx";
import {observer} from "mobx-react";
import axios from "axios";
import config from "../config";
import model from "../model";
import qs from 'querystring';
import "./chatview.css"
import Compose from "../components/compose"
import MessageList from "../components/messagelist"
import UserList from "../components/userlist"


class ChatView extends React.Component{

    state = {
        message: ''
    }

    componentDidMount(){
        model.connectToChat(config.API_Host + "/stream/" + model.state.user.token);
    }

    sendMessage(message){
        const body = {
            "message": message,
        }
        
        axios.post(config.API_Host + "/message", qs.stringify(body), {
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
                'Authorization': 'Bearer ' + model.state.user.token
            }
        }).then(action((res) => {
            console.log("response from /message: " + JSON.stringify(res.data));
            
            model.state.error = null;
        })).catch(action((err) => {
            console.log(err);
            model.state.error = "failed to add message, status code: " + err.response.status;
        }));
    }

    

    render() {

        return (
            <div className="container" style={{height:"100%"}}>
                <div className="page-header">
                    <h1>
                        CS291 Chat <small> by Dana Nguyen and Gareth George</small>
                    </h1>
                    <br/>
                </div>
                <div className="row" style={{height: "100%"}}>
                    <div className="col-md-10" style={{height: "100%"}}>
                        <h2>Messages</h2>
                        <MessageList/>
                        {model.state.connectedToChat ? <Compose sendMessage={this.sendMessage.bind(this)} /> : null}
                    </div>
                    <div className="col-md-2" style={{height:"100%"}}>
                        <div className="usercontainer">
                            <h2>Users</h2>
                            <UserList/>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default observer(ChatView);